// pages/home-acao.tsx

import React, { useState, useEffect } from 'react';
import Head from 'next/head';
import Layout from '../components/Layout';
import { supabase } from '../lib/supabaseClient';
import { useRouter } from 'next/router';
import Link from 'next/link';
import { Loader2, DollarSign, Users, AlertTriangle, Clock, CheckCircle } from 'lucide-react';
import type { Aluno } from '../types'; // Importa o tipo Aluno

// Tipagem para os dados do Dashboard
interface DashboardData {
  atrasadosCount: number;
  atrasadosValorTotal: number;
  proximosCount: number;
  emDiaCount: number;
  totalAlunos: number;
  planoAtual: 'free' | 'vip' | 'premium';
  limiteAlunos: number;
}

const HomeAcao: React.FC = () => {
  const router = useRouter();
  const [data, setData] = useState<DashboardData | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    // Define as datas para as queries dinâmicas
    const hoje = new Date();
    hoje.setHours(0, 0, 0, 0); 
    const hojeISO = hoje.toISOString().slice(0, 10);

    const cincoDias = new Date(hoje);
    cincoDias.setDate(hoje.getDate() + 5);
    const cincoDiasFrenteISO = cincoDias.toISOString().slice(0, 10);


    const fetchDashboardData = async () => {
      setLoading(true);
      setError(null);

      const { data: { user } } = await supabase.auth.getUser();

      if (!user) {
        router.push('/login');
        return;
      }

      try {
        // --- Executar todas as queries em paralelo ---
        const [
          treinadorPromise,
          alunosPromise // Busca todos os alunos UMA VEZ
        ] = await Promise.all([
          // 1. Busca dados do Treinador
          supabase
            .from('treinadores')
            .select('plano_atual, limite_alunos')
            .eq('id', user.id)
            .single(),
          
          // 2. Busca TODOS os alunos (para calcular no front-end)
          supabase
            .from('alunos')
            .select('status_mensalidade, data_vencimento_mensalidade, valor_mensalidade')
            .eq('id_treinador', user.id)
        ]);

        // --- Processar os Resultados ---

        // Treinador
        if (treinadorPromise.error) throw new Error("Erro ao buscar dados do plano.");
        const treinadorInfo = treinadorPromise.data;

        // Alunos
        if (alunosPromise.error) throw new Error("Erro ao buscar dados dos alunos.");
        const todosAlunos = alunosPromise.data || [];
        
        // --- Calcular Status em Tempo Real (mesma lógica do elenco.tsx) ---
        let atrasadosCount = 0;
        let atrasadosValorTotal = 0;
        let proximosCount = 0;
        let emDiaCount = 0;

        const hojeData = new Date();
        hojeData.setHours(0, 0, 0, 0);

        for (const aluno of todosAlunos) {
          const vencimento = new Date(aluno.data_vencimento_mensalidade + 'T00:00:00');
          vencimento.setHours(0, 0, 0, 0);
          
          if (aluno.status_mensalidade === 'pago') {
            emDiaCount++;
            continue;
          }

          // Se 'pendente', calcular:
          if (vencimento < hojeData) {
            atrasadosCount++;
            atrasadosValorTotal += aluno.valor_mensalidade || 0;
          } else {
            const cincoDias = new Date(hojeData);
            cincoDias.setDate(hojeData.getDate() + 5);
            if (vencimento >= hojeData && vencimento <= cincoDias) {
              proximosCount++;
            } else {
              // Pendente, mas futuro (mais de 5 dias)
              emDiaCount++;
            }
          }
        }

        // --- Atualizar Estado ---
        setData({
          atrasadosCount,
          atrasadosValorTotal,
          proximosCount,
          emDiaCount,
          totalAlunos: todosAlunos.length,
          planoAtual: treinadorInfo.plano_atual,
          limiteAlunos: treinadorInfo.limite_alunos,
        });

      } catch (err: any) {
        console.error("Erro no Dashboard:", err.message);
        setError("Não foi possível carregar os dados do painel.");
      } finally {
        setLoading(false);
      }
    };

    fetchDashboardData();
  }, [router]); // Roda quando a página carrega

  const formatCurrency = (value: number) => {
    return value.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' });
  };

  return (
    <>
      <Head>
        <title>Dribla | Zona de Ação</title>
      </Head>
      <Layout title="Zona de Ação Financeira">
        
        {loading && (
          <div className="flex justify-center items-center py-20">
            <Loader2 className="w-12 h-12 text-dribla-green animate-spin" />
          </div>
        )}

        {error && (
          <div className="p-4 mb-6 bg-dribla-orange/20 text-dribla-orange rounded-lg border border-dribla-orange text-center">
            {error}
          </div>
        )}

        {!loading && data && (
          <>
            <h1 className="text-3xl font-bold mb-8 text-white">Status Financeiro, Treinador!</h1>
            
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-12">
              {/* Card 1: INADIMPLÊNCIA (Vencidos) */}
              <div className="bg-gray-800 p-6 rounded-xl shadow-lg border-l-4 border-dribla-orange">
                <h2 className="text-lg font-semibold text-gray-200 mb-3 flex items-center">
                  <AlertTriangle className="w-5 h-5 mr-2 text-dribla-orange" /> Atrasados
                </h2>
                <p className="text-4xl font-extrabold text-dribla-orange mb-3">
                  {formatCurrency(data.atrasadosValorTotal)}
                </p>
                <p className="text-gray-400 mb-5 text-sm">{data.atrasadosCount} Alunos</p>
                <Link href="/elenco?filtro=vencida" className="btn-primary w-full !bg-dribla-orange !text-gray-900 hover:!bg-orange-500">
                  Resolver Agora
                </Link>
              </div>

              {/* Card 2: PRÓXIMOS VENCIMENTOS */}
              <div className="bg-gray-800 p-6 rounded-xl shadow-lg border-l-4 border-blue-400">
                <h2 className="text-lg font-semibold text-gray-200 mb-3 flex items-center">
                  <Clock className="w-5 h-5 mr-2 text-blue-400" /> Próximos a Vencer
                </h2>
                <p className="text-4xl font-extrabold text-blue-400 mb-3">{data.proximosCount}</p>
                <p className="text-gray-400 mb-5 text-sm">(Próximos 5 dias)</p>
                 <Link href="/elenco?filtro=proximo" className="btn-secondary w-full !bg-gray-700 hover:!bg-blue-400 hover:!text-gray-900">
                  Ver Alunos
                 </Link>
              </div>

              {/* Card 3: PAGOS (EM DIA) */}
              <div className="bg-gray-800 p-6 rounded-xl shadow-lg border-l-4 border-dribla-green">
                <h2 className="text-lg font-semibold text-gray-200 mb-3 flex items-center">
                  <CheckCircle className="w-5 h-5 mr-2 text-dribla-green" /> Em Dia
                </h2>
                <p className="text-4xl font-extrabold text-dribla-green mb-3">{data.emDiaCount}</p>
                <p className="text-gray-400 mb-5 text-sm">(Pagamentos recebidos)</p>
                 <Link href="/elenco?filtro=paga" className="btn-secondary w-full !bg-gray-700 hover:!bg-dribla-green hover:!text-gray-900">
                  Ver Alunos
                 </Link>
              </div>
            </div>

            {/* Seção de Ocupação do Time */}
            <div className="bg-gray-900 p-6 rounded-xl border border-gray-700 flex flex-col md:flex-row justify-between items-center gap-4">
              <div className='text-center md:text-left'>
                <h2 className="text-lg font-semibold text-white mb-1 flex items-center">
                  <Users className='w-5 h-5 mr-2 text-dribla-green'/> Status do seu Plano
                </h2>
                <p className='text-gray-400 text-sm'>
                  Plano: <span className='font-bold uppercase text-dribla-green'>{data.planoAtual}</span> | 
                  Ocupação: <span className='font-bold text-white'>{data.totalAlunos}</span> / <span className='text-gray-500'>{data.limiteAlunos === 99999 ? 'Ilimitado' : data.limiteAlunos}</span>
                </p>
              </div>
              <div className='flex space-x-4'>
                <Link href="/planos">
                    <button className='px-4 py-2 text-sm bg-gray-700 hover:bg-dribla-green hover:text-gray-900 rounded-lg transition duration-200'>
                        Fazer Upgrade
                    </button>
                </Link>
                 <Link href="/elenco">
                    <button className='btn-primary px-4 py-2 text-sm'>
                        Gerenciar Elenco
                    </button>
                 </Link>
              </div>
            </div>
          </>
        )}
      </Layout>
    </>
  );
};

export default HomeAcao;

